import React from 'react';
import './Cardss.css';
import CardsItem from './CardsItem';

function Cardss() {
  return (
    <div className='cardss'>
      
      <div className='cardss__container'>
        <div className='cardss__wrapper'>
          <ul className='cardss__items'>
            <CardsItem
              src='images/img-9.jpg'
              text='Rendang, hidangan yang luar biasa dari dapur Minangkabau.'
              label='Culinary'
              path='/'
            />
            <CardsItem
              src='images/img-2.jpg'
              text='Sate Padang, kelezatan yang terbungkus dengan aroma rempah yang memikatindera.'
              label='Culinary'
              path='/'
            />
          </ul>
          <ul className='cardss__items'>
            <CardsItem
              src='images/img-3.jpg'
              text='Soto Padang, hidangan hangat yang memikat selera dengan cita rasa rempah-rempahkhasMinang.'
              label='Culinary'
              path='/'
            />
            <CardsItem
              src='images/img-4.jpg'
              text='Gajeboh, camilan gurih yang menggoda dengan rempah-rempahkhasMinang'
              label='Culinary'
              path='/'
            />
            <CardsItem
              src='images/img-8.jpg'
              text='Keripik Sanjai, gurihnya cita rasa tradisi Minangkabau dalamsetiapgigitan'
              label='Culinary'
              path='/'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cardss;
